<template>
  <div class="cashier">
    <input type="text" v-model="inputValue" @input="onInput" class="input-box form-control"/>
    <div class="keyboard">
      <div v-for="(char, index) in chars.slice(0, 9)" :key="index" class="key-row">
        <button v-for="(charInRow, indexInRow) in char" :key="indexInRow" @click="inputChar(charInRow)">
          {{ charInRow }}
        </button>
        
      </div>
      <div>
      <button v-for="(char, index) in chars.slice(9, 12)" :key="index" @click="inputChar(char)">
          {{ char }}
        </button>
      </div>  
      
    </div>
    <div class="key-row1">
        
        <button class="clear-btn" @click="clear">清除</button>
        <button class="delete-btn" @click="deleteChar">删除</button>
        <button v-for="(char, index) in chars.slice(12, 15)" :key="index" @click="inputChar(char)">
          {{ char }}
        </button>
        <button @click="submit">提交</button>
      </div>
    
  </div>
  <div id="app">
    <input type="text" v-model="playerName" @input="fetchAvatar" placeholder="请输入玩家名字" />
    <img v-if="avatarUrl" :src="avatarUrl" alt="玩家头像" />
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      chars: '1234567890',
      inputValue: '',
      playerName: "",
      avatarUrl: "",
    };
  },

  methods: {
    
    
    async fetchAvatar() {
  try {
    const response = await axios.get(`https://skin.sparkpixel.ru/avatar/player/${this.playerName}`);
    return response.data.avatar;
  } catch (error) {
    console.error('获取头像失败:', error);
  }
},


    inputChar(char) {
      this.inputValue += char;
      this.onInput();
    },
    submit() {
      console.log("输入值既有内容且全部为数字");
      axios.get(`http://Recharge.sparkpixel.ru:27023/mtr/add/${this.inputValue.trim()}&${this.playerName.trim()}`, {
        headers: {
          'Content-Type': 'application/json',
        },
      })
      .then(response => {
        console.log(response.data);
        let string = response.data;
   
        alert(string);

      })
      .catch(error => {
        console.log(error);
      });
      this.inputValue = '';
    },
    clear() {
      this.inputValue = '';
      this.onInput();
      this.$emit('clear');
    },
    deleteChar() {
      this.inputValue = this.inputValue.slice(0, -1);
      this.onInput();
    },
    onInput() {
      this.$emit('input', this.inputValue);
    }
    
  },

};

</script>




